import React, { Component, useState, useEffect } from 'react';
import Konami from "konami";
import jsnes from "jsnes";
import Emulator from './Emulator';
import Runpage from './RunPage';
import data from './InterglacticTransmissing.nes';


export default function EasterEgg()  
  {

    useEffect(()=>{
        const easterEgg = new Konami(() => {
            var audio = new Audio('../sfx/egg_sfx.wav');
            audio.play();
            handleClick();
        });
      },[])

    const [isShown, setIsShown] = useState(false);

    const handleClick = event => {
        setIsShown(current => !current);
    };
 
    return (
       
            <div>
                {
                    isShown && (
                        <footer>
                        <div>
                            <Runpage />
                            <p>DPad: Setas<br/>Start: Enter, Select: CTRL Direito<br/>Botão A: Z, Botão B: X</p>
                        </div>
                        </footer>
                    ) 
                }
                {isShown && <EGG />}
            </div>
      
    );
  }

  function EGG() {

    
    return (
      <div>
        <footer>  
            
            {/* <p>DPad: Arrow keys<br/>Start: Return, Select: Tab<br/>A Button: A, B Button: S</p> */}
        </footer>
      
      </div>
    );
  }

